import {Button, Card, CardContent, CardHeader, Grid, Link, TextField, Typography} from "@material-ui/core";
import React, {useState} from "react";
import {Action} from "../Type/DispatchAction";

const LoginPage:React.FC<{setLogin:Action<boolean>}> = (props)=>{

    const [mail,setMail] = useState("");
    const [password,setPass] = useState("");

    return(
        <div>
            <Grid style={{marginTop:100}} container alignItems="center" justify="center">
                <Card variant="outlined" style={{width:'calc(100% - 50px)',maxWidth: 500}}>
                    <CardHeader style={{color:"#7c7a7a",textAlign:"center"}} title="ログイン" titleTypographyProps={{ variant: "h4"}}/>
                    <CardContent>
                        <TextField onChange={(e)=>{setMail(e.target.value)}} variant="outlined" label="メールアドレス" style={{width:'100%'}}/>
                        <div style={{marginTop:10}}/>
                        <TextField onChange={(e)=>{setPass(e.target.value)}} variant="outlined" label="パスワード" style={{width:'100%'}}/>
                        <div style={{marginTop:10}}/>
                        <Button onClick={
                            (e)=>{
                                console.log(mail+' '+password)
                                props.setLogin(true);
                            }} style={{
                                width:'100%',
                                color:"white",
                                backgroundColor:"#0072d7"
                            }} variant="contained">ログイン</Button>
                        <div style={{marginTop:10}}/>
                        <Link href="https://www.google.com">パスワードがわかりませんか?</Link>
                    </CardContent>
                </Card>
            </Grid>
        </div>
    );
};

export default LoginPage;
